.WelcomeScreen {
    padding: 1rem;
    position: fixed;
    width: 100%;
    height: 100%;
    text-align: center;
    top: 0;
    background: rgb(250, 249, 249);
}
.WelcomeScreen h1 {
    color: var(--primary-color);
}
.login-button {
    color: #494949;
    text-transform: uppercase;
    text-decoration: none;
    background: #ffffff;
    border: 4px solid #494949;
    display: inline-block;
    transition: all 0.4s ease 0s;
    min-width: 10rem;
    margin: 1rem;
}
.button_cont {
    margin: auto;
    max-width: 100%;
    width: 200px;
}
.google-btn {
    width: 184px;
    height: 42px;
    margin: 1rem;
    background-color: #4285f4;
    background-color: var(--secondary-color);
    border-radius: 2px;
    box-shadow: 0 3px 4px 0 rgba(0, 0, 0, 0.25);
}
.google-btn:hover {
    box-shadow: 0 0 6px #4285f4;
    box-shadow: 0 0 6px var(--secondary-color);
}
.google-btn:active {
    background: #1669f2;
    background: var(--secondary-color);
}
.google-icon-wrapper {
    position: absolute;
    margin-top: 1px;
    margin-left: 1px;
    width: 40px;
    height: 40px;
    border-radius: 2px;
    background-color: #fff;
}
.google-icon {
    position: absolute;
    margin-top: 11px;
    margin-left: -8px;
    width: 18px;
    height: 18px;
}
.btn-text {
    float: right;
    color: #fff;
    font-size: 14px;
    letter-spacing: 0.2px;
    font-family: "Roboto";
    background-color: #4285f4;
    background-color: var(--secondary-color);
    width: 80%;
    height: 100%;
    text-align: center;
}
.btn-text:hover {
    cursor: pointer;
}